/* GENERAL */
.tengeru-div{
    position: relative;
    /* top: -3rem; */
    font-family:Verdana, Geneva, Tahoma, sans-serif !important;
}
.TZsubheader{
    font-size: 2vw;
    text-align: left;
    margin-left: 4vw;
    margin-top: -1rem;
}
.TZsubheader2{
    font-size: 1.5vw;
    text-align: left;
    margin-left: 4vw;
}
.TZsubheader3{
    font-size: 1.5vw;
    text-align: left;
    margin-left: 4vw;
}
.general-format{
    padding-left: 4vw;
    padding-right: 4vw;
}

/* HEADER IMAGE SECTION */
.header-block-div{
    position: relative;
    height: 40vw;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.tengeru-div .header-block-div:before{
    content: "";
    background-image: url(../../images/TZ-kili_sunrise.JPG);
    background-size: cover;
    position: absolute;
    background-repeat: none;
    background-position: 50% 60%;
    background-attachment: fixed;
    opacity: 0.9;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    opacity: 0.5;
}
.TZ-project-title{
    position: relative;
    color: black;
    font-size: 3vw;
    text-align: left;
    margin-left: 15vw ;
    margin-right: 30vw;
    margin-top: 10px;
}
.TZ-project-title-sm{
    position: relative;
    color: black;
    font-size: 1.8vw;
    text-align: left;
    margin-left: 15vw;
    margin-right: 50vw;
    margin-bottom: 10px;
}
/* BACKGROUND SECTION */
.TZbackground-div{
    display: flex;
    justify-content: left;
    flex-direction: row;
    padding-top: 1rem;
}
.TZbackground-text {
    font-size: calc(12px + 0.25vw);
    text-align: left;
    padding-top: 0.5rem;
    padding-bottom: 0rem;
    padding-left: 4vw;
    padding-right: 2vw;
}
.TZbackground-img{
    position: relative;
    width: 40%;
    height: 40%;
    margin-top: auto;
    margin-bottom: auto;
    padding-top: 1rem;
    padding-left: 2vw;
    padding-right: 4vw;
}
/* WORK SECTION */
.TZ-work-div{
    height: 36vw;
}
.work-circles{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;
    max-width: 50vw;
    margin-left: 4vw;
    margin-top: 4vw;
}
.repair-circle{
    position: relative;
}
.circle1 {
    position: absolute;
    width: 16vw;
    height: 16vw;
    border-radius: 50%;  
    box-shadow: 0px 0px 8px 0px #000000;
}
.circle1-area {
    width: 18vw;
    height: 18vw;
    position: absolute;
    border-radius: 50%;
    background-color: rgba(100, 100, 100, 0.534);
}
.TZcircle1-text{
    position: absolute;
    font-size: 25px;
    color: white;
    margin-top: 8vw;
    margin-left: 5.5vw;
}
.TZcircle2-text{
    position: absolute;
    font-size: 25px;
    color: white;
    margin-top: 8vw;
    margin-left: 6vw;
}
.TZcircle3-text{
    position: absolute;
    font-size: 25px;
    color: white;
    margin-top: 8vw;
    margin-left: 5vw;
}
.work-text-bullets{
    position: absolute;
    width: 16vw;
    margin-top: 17vw;
}


/* PARTITIONS SECTION */
.TZpartitions-div{
    padding-top: 2vw;
}
.TZpartitions-text{
    font-size: calc(12px + 0.25vw);
    text-align: left;
    padding-left: 4vw;
    padding-right: 4vw;
}
.TZpartitions-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    grid-auto-flow: row; 
    justify-content: space-between;
    margin: 0 0 0.2rem 0;
    padding-bottom: 1rem;
    padding-left: 4vw;
    padding-right: 4vw;
}


.TZaccordion-div{
    position: absolute;
    max-width: 20vw;
    top: -16%;
    /* background-color: rgb(182, 202, 223, 0.5) !important; */
    background-color:rgba(131, 130, 130, 0.164) !important;
}
.TZprocess-area{
    position: relative;
    max-width: 20vw;
    height: 100%;
    box-shadow: 0px 0px 8px 0px #000000;
}
.TZprocess-title{
    position: relative;
    left: 50%;
    /* top: 10%; */
    padding-top: 0px;
    transform: translate(-45%, -10%);
    color: white;
    font-weight: bold;
    font-size: calc(14px + 0.4vw);
    width: 100%;
    text-align: center !important;
}
.TZprocess-img{
    display: block;
    width: 100%;
    margin: 0px;
}
.TZprocess-text{
    position: relative !important;
    text-align: left;
    color: black;
    font-size: calc(12px + 0.2vw) !important;
    /* background-color:rgb(192, 188, 188); */
}

/* RESULTS DIV */

.TZpartitions-img{
    width: 68vw;
    height: auto;
    padding-top: 1vw;
    padding-left: 2vw;
    padding-right: 2vw;
}
/* LESSONS SECTION */
.TZlessons-div{
    display: flex;
    justify-content: left;
    flex-direction: row;
    padding-top: 1rem;
}
.TZlessons-text{
    font-size: calc(12px + 0.25vw);
    text-align: left;
    padding-bottom: 2px;
    padding-left: 4vw;
    padding-right: 2vw;
}
.TZlessons-img-div{
    display: grid;
    grid-auto-flow: row;
}
.TZlessons-img{
    width: 31vw;
    height: auto;
    margin-top: auto;
    margin-bottom: auto;
    padding-top: 3rem;
    padding-left: 2rem;
    padding-right: 4vw;
}

