/* HEX GRID*/

.home-projects-div{
  margin-left: 8vw;
  margin-right: 8vw;
  margin-bottom: 4vw;
}

#hexGrid {
  overflow: hidden;
  width: 90%;
  margin: 0 auto;
  padding:0.707% 0;
}

#hexGrid:after {
  content: "";
  display: block;
  clear: both;
}

.hex {
  position: relative;
  list-style-type: none;
  float: left;
  overflow: hidden;
  visibility: hidden;
  transform: rotate(-60deg) skewY(30deg);
}

.hex * {
  position: absolute;
  visibility: visible;
}

.hexIn {
  display:block;
  width: 100%;
  height: 100%;
  text-align: center;
  color: #fff;
  overflow: hidden;
  transform: skewY(-30deg) rotate(60deg);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}


/* HEX CONTENT */

.hex img {
  left: -100%;
  right: -100%;
  width: auto;
  height: 100%;
  margin: 0 auto;
}

.hex h1, .hex p {
  width: 90%;
  padding: 0 5%;
  background-color:rgb(128, 128, 128);
  background-color: rgba(104, 103, 103, 0.75);
  font-family: 'Raleway', sans-serif;
  transition: top .2s ease-out, bottom .2s ease-out, .2s padding .2s ease-out;
}

.hex h1 {
  bottom: 110%;
  /* font-size: 22px !important; */
  font-size: 1.5vw !important;
  font-weight: normal;
  font-size: 1.5em;
  padding-top: 100%;
  padding-bottom: 90%;
  margin-bottom: -5px !important;
}

.hex p {
  margin-top: 5px !important;
}


.hex h1:after {
  content: '';
  display: block;
  position: absolute;
  bottom: -1px;
  left: 45%;
  width: 10%;
  text-align: center;
  z-index: 1;
  border-bottom: 2px solid #fff;
}

.hex p {
  padding-top: 50%;
  top: 110%;
  padding-bottom: 50%;
  /* font-size: 14px; */
  font-size: 0.95vw;
}

.hex:hover{
  cursor: pointer;
}


/* HOVER EFFECT  */

.hexIn:hover h1 {
  bottom: 50%;
  padding-bottom: 10%;
}

.hexIn:hover p {
  top: 50%;
  padding-top: 10%;
}

