.images-intro-text{
    font-size: calc(12px + 0.25vw);
    text-align: left;
    padding-top: 0.5rem;
    padding-bottom: 0rem;
    padding-left: 2vw;
    padding-right: 2vw; 
}
.images-div {
    padding-bottom: 3rem;
    margin-left: 4vw;
    margin-right: 4vw;
    overflow: visible;
}
.photos-flex-div{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    flex: 1 1 21%;
    margin-left: 1vw;
    margin-right: 1vw;
}
.photo{
    max-width: 20vw;
    margin: 1rem;
    transition: transform 0.25s ease;
}
.photo:hover{
    position: relative;
    cursor: pointer; /* or some other value */
    transform:scale(2);
    z-index: 999;
}
